@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
html,
body {
  margin: 0;
}

html,
body,
button,
center {
  font-family: "Quicksand", sans-serif;
}

.Key {
  height: 6vh;
  border-radius: 10%;
  margin: 0.5%;
  position: relative;
  border: 2px solid #d0d3d4;
  outline: none;
  font-size: 15px;
}

.Keyboard {
  margin: auto;
  width: 90%;
  height: 30vh;
  top: 50vh;
  padding: 1%;
  position: relative;
}

.Keyboard .row {
  width: 100%;
  display: flex;
  position: relative;
}

.btn-begin {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-begin > span.countdown {
  top: 12vh;
  position: absolute;
  color: #2e86c1;
  font-size: 25px;
}

.btn-begin > span.timer {
  top: 12vh;
  position: absolute;
  color: #7f8c8d;
  font-size: 20px;
}

.btn-begin button {
  z-index: 99;
  width: 130px;
  height: 40px;
  top: 12vh;
  border-radius: 10%;
  position: absolute;
  color: white;
  font-size: 15px;
  background-color: #2e86c1;
  border: none;
  cursor: pointer;
}

.btn-begin > button:hover {
  background-color: #aed6f1;
}

.BibleVerse {
  position: absolute;
  top: 15vh;
  width: 80%;
  padding: 30px;
  line-height: 2;
  font-size: 25px;
}

span.typed {
  color: #2e86c1;
}

.btn-group-bible {
  padding: 10vh 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.btn-group-bible .btn-testament {
  position: relative;
  top: 20vh;
  margin: 5vw;
  width: 30vw;
  height: 30vh;
  background-color: #5d6d7e;
  box-shadow: 5px 5px #273746;
  border: none;
  font-size: 3vh;
  color: white;
  outline: none;
}

.btn-group-bible .btn-testament:hover {
  background-color: #f8c471;
  box-shadow: 5px 5px #273746;
  color: #5d6d7e;
  cursor: pointer;
}

.btn-group-bible .btn-book,
.btn-chapter {
  margin: 2vw;
  width: 25vw;
  height: 7vh;
  background-color: #5d6d7e;
  box-shadow: 5px 5px #273746;
  border: none;
  font-size: 2.5vh;
  color: white;
  outline: none;
}

.btn-group-bible .btn-book:hover {
  background-color: #aed6f1;
  box-shadow: 5px 5px #273746;
  color: #5d6d7e;
  cursor: pointer;
}

.btn-group-bible .btn-chapter:hover {
  background-color: #f1948a;
  box-shadow: 5px 5px #273746;
  color: #5d6d7e;
  cursor: pointer;
}

.btn-group-verses {
  padding: 10vh 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0 20px;
  outline: none;
}

.btn-group-verses .verse-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-group-verses .verse-wrap p {
  width: 80vw;
  padding: 20px;
}

.btn-group-verses .verse-wrap .btn-verse {
  height: 6vh;
  display: flex;
  align-items: center;
  text-align: center;
  cursor: pointer;
  border: none;
  background-color: #5d6d7e;
  box-shadow: 5px 5px #273746;
  color: white;
  outline: none;
}

.btn-group-verses .verse-wrap .btn-verse:hover {
  background-color: #58d68d;
  color: #5d6d7e;
}

.verse-wrap link {
  text-decoration: none;
}

.verse-info {
  display: flex;
  align-items: center;
  position: absolute;
  top: 25px;
  right: 30px;
  color: #616a6b;
}

.btn-back {
  position: absolute;
  top: 25px;
  right: 30px;
  color: #616a6b;
}

#btn-hint {
  position: absolute;
  top: 45vh;
  left: 5vw;
  color: #d4ac0d;
}

#img-hands {
  width: 300px;
}

/* Copied from react-burger-menu docs and customized */
#App {
  font-family: "Quicksand", sans-serif;

  /* Give app full page to work with */
  height: 100vh;
}

#page-wrap {
  text-align: center;

  /* Prevent sidebar from showing a scrollbar on page */
  overflow: auto;
}

/* Individual item */
.bm-item {
  display: flex !important;
  align-items: center !important;
  text-decoration: none;
  margin: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
  outline: none;
}

/* Change color on hover */
.bm-item:hover {
  text-decoration: none;
  color: white;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 26px;
  height: 20px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #34495e;
  padding: 4.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

